  const createTransaction = async (values, jobID) => {
    let yearVal = expiry.trim()
    yearVal = yearVal.substring(yearVal.length-2,yearVal.length);
    let expiryFormatted = '20'+yearVal+'-'+expiry.substring(0,2)
    let cardNumber = numberCard;
    cardNumber = cardNumber.replace(/ /g, "");
    let authorizeFilds = {
      "createTransactionRequest": {
          "merchantAuthentication": {
            "name": merchantName,
            "transactionKey": merchantKey
          },
          "refId": jobID,
          "transactionRequest": {
              "transactionType": "authCaptureTransaction",
              "amount": formatPrice((getTotalPriceWithDiscount( getTotalPrice()) * ((taxPercent / 100) + 1) ) ),
              "payment": {
                  "creditCard": {
                      "cardNumber": cardNumber,
                      "expirationDate": expiryFormatted,
                      "cardCode": cvc
                  }
              },
              "tax": {
                "amount": formatPrice((getTotalPriceWithDiscount( getTotalPrice()) * ((taxPercent / 100) + 1) ) ),
                "name": "voing tax",
                "description": `${taxPercent} %`
              }
              // ,
              // "billTo": getBillTo(values),
              // "shipTo": getShipTo(values)
          }
        }
      }

      authorizeFilds = JSON.stringify(authorizeFilds)

      try {                           
        const res = await axios.post(authNetURL, authorizeFilds, {
          headers: {
          'Content-Type': 'application/json',
          }
        });
        if(res.data.messages.resultCode === 'Error' || res.data.transactionResponse.responseCode!=='1' ){
          return null;
        }
        else{
          return res.data.transactionResponse.transId
        }
      } catch (err) {
        console.log('Error createAuthorize >> ', err)
        return null;
      }
  }

